"use client";
import { useParams } from "next/navigation";
import scss from "./NewsDetailContent.module.scss";
import { useGetDetNewsQuery } from "@/redux/api/news";
import Image from "next/image";

const NewsDetailContent = () => {
  const { newsDetail } = useParams();
  const { data } = useGetDetNewsQuery(String(newsDetail));
  // console.log(data, "data");

  return (
    <div className={scss.NewsDetailContent}>
      <div className="container">
        <div className={scss.content}>
          <h1>{data?.description}</h1>

          <Image
            src={data?.image!}
            alt="img"
            width={700}
            height={500}
            quality={70}
            property="img"
          />
          <p>{data?.content}</p>
          <div></div>
          <div>Комментарии:</div>
          {/* {data?.comments?.map((comment) => (
            <div key={comment.id}>
              <h3>{comment.title}</h3>
              <div>{comment.content}</div>
              <div>Автор: {comment.author}</div>
              <div>Дата публикации: {comment.date}</div>
            </div>
          ))} */}
        </div>
      </div>
    </div>
  );
};

export default NewsDetailContent;
